.QueryBuilder {
    width: 80%;
    height: 60%;
    border-radius: 10px;
    padding: 2% 2% 2% 2%;
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    color: white;
    margin: auto;
}

.GroupBlock {
    border-style: double;
    background-color: #303030;
    color: white;
    border-width: thin;
    padding: 10px 10px 10px 10px;
    border-radius: 10px;
    margin: 10px 10px 10px 10px;
}

.query {
   font-weight: bold;
   padding: 10px 10px 10px 10px; 
   background-color: blue;
   color: white;
}

.Button {
    margin: 1% 1% 1% 1%;
    background-color: blue;
    padding: 1% 1% 1% 1%;
    color:white;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
}

.Conjunction {
    width: 10%;
    background-color: black;
    color: white;
    margin: 1% 1% 1% 1%;
    padding: 1% 1% 1% 1%;
    font-weight: bold;
}